@keyframes load {
    0%{
        opacity: 0.08;
        filter: blur(5px);
        letter-spacing: 3px;
    }
    100%{
    }
}

.animate {
    display:flex;
    justify-content: center;
    margin-top: 10vh;
    align-items: center;
    height:20vh;
    font-family: Helvetica, sans-serif, Arial;
    animation: load 1.2s infinite 0s ease-in-out;
    animation-direction: alternate;
    text-shadow: 0 0 1px white;
}
